import { useState } from 'react';
import './Portfolio.css';
import TopSection from './Header/Header.js'
import BotSection from './Contents/Contents.js'
import Links from './Link/Links.js'
import { animateScroll as scroll } from "react-scroll";

function Portfolio() {
    const [neon, setNeon] = useState('name');
    const [links, setLinks] = useState(false);
    const [rain, setRain] = useState(true);
    function stopRain() {
        setRain(false);
        scroll.scrollTo(window.innerHeight, {
            smooth: 'easeInQuad',
            duration: 1000,
        })
    }
    function delay(t, v) {
        return new Promise(resolve => setTimeout(resolve, t, v));
    }
    function startRain() {
        scroll.scrollTo(-window.innerHeight, {
            smooth: 'easeInQuad',
            duration: 1000,
        })
        delay(3000).then(()=> {
            setRain(true);
        }); 
    }
    function changeNeon(letter) {
        setNeon(letter);
    }
    if (!links) {
        setTimeout(function() {
            console.log("TIMEOUTCALLED")
            setLinks(true);
        }, 5000);
    }

    return (
        <div className={"pf-body"}>
            {links?<Links handleChange={changeNeon} stopRain={stopRain}/>:''}
            <TopSection neon={neon} rain={rain}/>
            {rain?'':<BotSection preventMultiple={true} startRain={startRain}/>}
        </div>
    )
}

export default Portfolio;