import React, { useState } from 'react';
import './Contents.css';
import CONSTANTS from '../../Constants.json';
import jsgradient from '../../external-libraries/jsGradient.js';

class BotSection extends React.Component {
    constructor(props) {
        super(props);
    }
    shouldComponentUpdate(n) {
        if (n.preventMultiple == this.props.preventMultiple) return false;
        return true;
    }
    render() {
        return (
            <div className={'pf-bot-section'}>
                <Contents />
                <Fishes fishes={25}/>
                <Bubbles bubbles={50} />
                <Rapture towers={36} startRain={this.props.startRain} />
            </div>
        )
    }
}

function Rapture({towers, startRain}) {
    const max_z = 10;
    const gradients = jsgradient.generateGradient(CONSTANTS.BLACK, CONSTANTS.RAPTURE_DARK_BLUE, max_z+1);
    const MakeStacks = (n, thr, h, fac=3, style=[]) => {
        let acc_h = 0;
        return Array.from(Array(n)).map((_, i)=>{
            const w_fac = i<=thr?1:fac;
            const stack_w = (80/(n+1)/w_fac)*(n+1-i);
            const stack_l = (100-stack_w)/2;
            const h_fac = i<=thr?1:fac;
            const stack_h = (h_fac*1)+Math.pow((i-n)/(Math.pow(Math.pow(n,4)/(h-1),1/4)),4);
            acc_h += stack_h;
            return (
                <div key={i} className={'tower-stacks'} style={{
                    width: `${stack_w}%`,
                    left: `${stack_l}%`,
                    height: `${stack_h}vh`,
                    top: `-${acc_h}vh`,
                    ...style,
                }}/>   
            )
        })
    };
    const MakeTowers = (n, l_offset, s_offset = 20) => {
        return Array.from(Array(n)).map((_, i) => {
            const w = 1+Math.random()*5;
            const spread = (i>=towers/2?l_offset+Math.random()*(50-l_offset):50+Math.random()*(50-l_offset));
            const h_curvature = 5;
            const z = (max_z/2)+Math.round((max_z/2)*Math.pow((spread-50)/(50-l_offset), 2));
            const rand_stacks = Math.round(Math.random()*4)+2;
            const duration = Math.round(Math.random()*10)+1;
            return (
                <div className={'tower-container'} key={i}>
                    <div className={'tower'} style={{
                        left: `${spread-w/2}%`,
                        width: `${w}%`,
                        height: `${100-Math.pow((spread-50)/h_curvature, 2)}%`,
                        zIndex: z+Math.round(Math.random())-2,
                        animationDuration: `${duration}s`,
                        background: gradients[z-(max_z/2)],
                    }}>
                        {MakeStacks(rand_stacks, Math.round(Math.random() * rand_stacks), 5, 5, {background: gradients[z-5]})}
                    </div>
                </div>
            )
        });
    }   
    return (
        <div className={'rapture-container'}>
            <div key={'rapture-main'} className={'rapture-main'} 
                style={{
                    zIndex: max_z/2, borderRadius:'5%'
                }}>{MakeStacks(4, 2, 5, 5, {borderRadius:'5% 5% 0% 0% / 100% 100% 0% 0%',background: CONSTANTS.RAPTURE_SEA_GREEN})}
                <div className={'lighthouse-beacon'} onClick={()=>{startRain();}} />
            </div>
            {MakeTowers(36, 10)}
            <div className={'fathomless-depths'} />
        </div>
    )
}

function Bubbles({bubbles}) {
    return <div className={'bubbles-container'} style={{zIndex: 10}}>
            {Array.from(Array(bubbles)).map((_, i)=>{
                    const r = Math.round(Math.random() * 10) + 1;
                    const l = Math.floor(Math.random() * 100);
                    const d = Math.round(Math.random() * 20) + 10;
                    const dt = Math.round(Math.random() * 20) + 1;
                    return (
                        <div key={i} className={'bubble'} style={{
                            left: `${l}%`,
                            width: `${r}px`,
                            height: `${r}px`,
                            animationDelay: `${d}s`,
                            animationDuration: `${dt}s`,
                        }}/>
                    )
                })}
        </div>
}

function Fishes({fishes}) {
    return <div className={'fishes-container'}>
            {Array.from(Array(fishes)).map((_, i) => {
                const LoR = Math.random()>=0.5?1:-1;
                const z = Math.round(Math.random()*10);
                const scale = 0.2+z/20;
                const d = (Math.random()*50)+100;
                const dt = (Math.random()*20)+(Math.random()*10)+20;
                const schools = Math.round(Math.random()*5)+1;
                return (
                    <div className={'fish-school'} key={i} style={{
                            top: `${75+Math.random()*20}%`,
                            marginLeft: LoR==1?`-${z}%`:'-105%',                  
                            animationName: LoR==1?'swim':'swim-reverse',
                            animationDelay: `${dt}s`,
                            animationDuration: `${d}s`,
                            zIndex: z,
                            opacity: 0.5+z/10, 
                    }}>
                        {Array.from(Array(schools)).map((_, j) => { 
                            return <div key={j} className={'fish'} style={{
                                marginLeft: `${Math.random()*z/2}vh`,
                                marginTop: `${Math.random()*z/2}vh`,
                                transform: LoR==1?`scale(${scale})`:`scale(${scale}) rotateY(180deg)`,
                            }}/>
                        })}
                    </div>
                )
            })}
        </div>
}

function Contents() {
    const static_contents = [
        {
            'titles': ['Software Engineer', 'with a minor in mathematics'],
            'contents': [
                'Arts & Music enthusiast.',
                'Designed and developed the website with ReactJS.',
                'Rain, bubbles, fishes, and buildings were randomly generated with parabolic equations.',
                'All icons and shapes made purely from CSS; no image files were stored in repository.'
            ],
        },
        {
            'titles': ['Education'],
            'contents': [
                'University of Maryland — 2016-2019',
                'Walla Walla Community College — 2015-2016',
                'Catholic Junior College — 2013-2014',
                'Saint Joseph\'s Institution — 2011-2012',
            ]
        }
    ];
    const skills_contents = {
        'javascript':{proficiency: 4},
        'css':{proficiency: 4},
        'reactjs':{proficiency: 4, children:[<div className={'base'}/>]},
        'html':{proficiency: 4},
        'java':{proficiency: 3},
        'c':{proficiency: 1},
        'c#':{proficiency: 1},
        'python':{proficiency: 4, children:[<div className={'snake'}/>,<div className={'twin'}/>, <div className={'eye'}/>,<div className={'eye2'}/>]},
        'golang':{proficiency: 4},
        'git':{proficiency: 2},
        'nodejs':{proficiency: 1},
        'ocaml':{proficiency: 1},
        'rust':{proficiency: 1},
        'ruby':{proficiency: 1},
        
    }
    let id = 0;
    const [active, setActive] = useState(-1);
    function MakeStaticContents() {
        return static_contents.map((c, i) => {
            return <ContentPanel key={i} id={id++} active={active} setActive={setActive} content={c}/>
        })
    }
    return (
        <div className={"panels-container"}>
            {MakeStaticContents()}
            {<SkillPanel id={id++} active={active} setActive={setActive} content={skills_contents} />}
        </div>
    ) 
}

function SkillPanel({content, id, active, setActive}) {
    function handleClick() {
        setActive(id)
    }
    return <div className={'content-panel'} onMouseDown={handleClick}>
        <div className={'content-title'}>{'Skills'}</div>
        {active==id?<div className={'skills-container'}>
            {Object.keys(content).map((k, i) => {
                const s = content[k];
                return <PersonalSkill key={i} tag={k} data={s} />
            })}
        </div>:''}
    </div>
}

function PersonalSkill({tag, data}) {
    const proficiency_color = {
        0: '#f54029',
        1: '#d68b01',
        2: '#FFFF00',
        3: '#2db301', 
        4: '#2b0087',
    }
    return <div className={`personal-skill-icon`}>
        <div className={`${tag}-container skill`}>
            {data.children}
        </div>
        <div className={'proficiency-text'}>{`${tag.toUpperCase()} LVL ${data.proficiency}`}</div>
    </div>
}

function ContentPanel({id, content, active, setActive}) {
    function handleClick() {
        setActive(id)
    }
    return <div className={'content-panel'}
        onMouseDown={handleClick}>
        {content.titles.map((title, j) => {
            return <div key={j} className={'content-title'}>{title}</div>
        })}
        {active==id?<div className={'contents-container'}>
            {content.contents.map((c, k)=> {
                return <div key={k} className={'content'}>{c}</div>
            })}
        </div>:''}
    </div>
}

export default BotSection;