import './Links.css';
import { useState } from 'react';
import { motion } from 'framer-motion';

const resumeDocx = import('../assets/resume.docx')
const linkIconOnHover = {scale: 1.5, opacity: 1}
const linksMouseEnter = (handleChange, letter) => {
    handleChange(letter);
}
const copyTextClipboard = (text, e, change) => {
    e.preventDefault();
    if (!e.clipboardData) return;
    switch(text) {
        case 'phone':
            e.clipboardData.setData("text/plain", '+6281288881024');
            change('clipboard')
            return;
        case 'email':
            e.clipboardData.setData("text/plain", 'william.sentosatio@hotmail.com');
            change('clipboard')
        default:
            return;
    }
}

function Links({handleChange, stopRain}) {
    return (
        <div className={'pf-link'}>
            <AboutIcon handleChange={handleChange} stopRain={stopRain}/>
            <LinkedInIcon handleChange={handleChange}/>
            <GithubIcon handleChange={handleChange}/>
            <ResumeIcon handleChange={handleChange}/>
            <PhoneIcon handleChange={handleChange}/>
            <EmailIcon handleChange={handleChange}/>
        </div>
    )
}

function LinkedInIcon({handleChange}) {
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'} 
            onMouseEnter={() => {linksMouseEnter(handleChange, 'linkedin')}}>
            <div className={'linkedin-border'}/>
            <div className={'linkedin-container link-icon'} onClick={()=>{window.open('https://www.linkedin.com/in/william-sentosatio/')}}>
                <div className={'linkedin-i'}/>
                <div className={'linkedin-n'}/>
            </div>
        </motion.div>
    )
}

function GithubIcon({handleChange}) {
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'}
            onMouseEnter={() => {linksMouseEnter(handleChange, 'github')}}>
            <div className={'gh-border'} />
            <div className={'github-container link-icon'} onClick={()=>{window.open('https://github.com/povertycycle')}}>
                <div className={'gh-body'}/>
                <div className={'gh-body-l'}/>
                <div className={'gh-body-l-none'}/>
                <div className={'gh-body-r'}/>
                <div className={'gh-body-r-none'}/>
                <div className={'gh-neck'}/>
                <div className={'gh-head'}/>
                <div className={'gh-head-l'}/>
                <div className={'gh-head-r'}/>
                <div className={'gh-l-ear'}/>
                <div className={'gh-r-ear'}/>
                <div className={'gh-tail-e'}/>
                <div className={'gh-tail-b'}/>
                <div className={'gh-tail-e-none'}/>            
                <div className={'gh-tail-b-none'}/>
                <div className={'gh-tail-t'}/>
            </div>
        </motion.div>
        
    )
}

function ResumeIcon({handleChange}) {
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'}
            onMouseEnter={() => {linksMouseEnter(handleChange, 'resume')}}>
            <div className={'cv-border'}/>
            <div className={'cv-flip'}>
                <div className={'cv-strike'}/>
            </div>
            <div className={'cv-container link-icon'} onClick={()=>{window.open(resumeDocx)}}>
                <div className={'cv-lines'}>
                    {Array.apply(null, Array(4)).map((item, i)=>{
                        return (
                            <div key={i} className={'cv-line'} style={{width: i == 3 ? '80%' : '100%'}}/>
                        )
                    })}
                </div>
            </div>
        </motion.div>
    )
}

function AboutIcon({handleChange, stopRain}) {
    function displayBottom() {
        stopRain();
    }
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'}
            onMouseEnter={() => {linksMouseEnter(handleChange, 'about')}}>
            <div className={'about-container link-icon'} onClick={()=>{displayBottom()}}>
                <div className={'about-roof-house'} />
                <div className={'about-roof-l'} />
                <div className={'about-roof-r'} />
                <div className={'about-base-house'} />
                <div className={'about-chimney'} />
                <div className={'about-head'} />
                <div className={'about-body'} />
            </div>
        </motion.div>
    )
}

function PhoneIcon({handleChange}) {    
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'}
            onMouseEnter={() => {linksMouseEnter(handleChange, 'phone')}}>
            <div className={'phone-border'} />
            <div className={'phone-container link-icon'} onCopy={(e)=>{copyTextClipboard('phone', e, handleChange);}}  onClick={()=>{document.execCommand("copy")}}>
                <div className={'phone-holder'} />
                <div className={'phone-holder-none'} />
                <div className={'phone-ear'} />
                <div className={'phone-mouth'} />
            </div>
        </motion.div>
    )
}

function EmailIcon({handleChange}) {
    return (
        <motion.div whileHover={linkIconOnHover} className={'icon-con'}
            onMouseEnter={() => {linksMouseEnter(handleChange, 'email')}}>
            <div className={'email-border'} />
            <div className={'email-container link-icon'} onCopy={(e)=>{copyTextClipboard('email', e, handleChange);}}  onClick={()=>{document.execCommand("copy")}}>
                <div className={'email-shape'} />
            </div>
        </motion.div>
    )
}

export default Links;