import { useState } from 'react';
import './Header.css';
import '../ext/neon.css'
import '../ext/rain.css'

const CONTACTS = {
    phone: '+(62) 812 8888 1024',
    email: 'william . sentosatio @ hotmail.com'
};

function Header({neon}) {
    const [firstTimeLoad, setDone] = useState(true);
    const HEADER_NEON_MAP = {
        'about': ['About'],
        'name': ['Will', 'i', 'am', 'Sento', 'sa', 'tio'],
        'linkedin': ['LinkedIn'],
        'github': ['GitHub'],
        'resume': ['Resume'],
        'phone': CONTACTS.phone.split(' '),
        'email': CONTACTS.email.split(' '),
        'clipboard': 'Copied to clipboard'.split(' '),
    }
    function displayText() {
        return HEADER_NEON_MAP[neon].map((s, i)=>{
            return (
                <div className={firstTimeLoad ? 'neon-ltr first-time-load' : 'neon-ltr stable-load'} key={i}>
                    <span className={'neon-str'}>{s}</span>
                    <span className={'neon-lg-rflc'}>{s}</span>
                </div>
            )
        });
    }

    if (firstTimeLoad) {
        setTimeout(function() {
            setDone(false);
        }, 10000)
    }
   
    return (
        <div className={'pf-header'}>
            <div className={'neon-lg'}>
                <b className={'b-neon'}>
                    {displayText()}
                </b>
            </div>
        </div>
    )
}

function Rain({rain}) {
    const NUMBER_OF_RAINDROPS = 50;
    return (
        <div className={'rain-container'}>
            {rain?Array.apply(null, Array(NUMBER_OF_RAINDROPS)).map((item, i)=>{
                const l = window.innerWidth<=700?Math.floor(Math.random()*100):Math.floor(Math.random()*60)+20;
                const d = (Math.random()*5)+5;
                const dt = Math.random()+1;
                const t = Math.floor(Math.random()*100);
                return (
                    <div key={i}>
                        <div className={'raindrop'} 
                            style={{
                                animationDelay: `calc(${d} * 1s)`,
                                animationDuration: `calc(${dt} * 1s)`,
                                left: `calc(${l} * 1%)`,
                                top: `calc((${t} + 50) * -1px)`,
                                transform: `scaleY(calc(${Math.random()}))`,
                                opacity: Math.random(),
                            }}/>
                        <div className={'wave'}
                            style={{
                                animationDelay: `calc(${d + dt} * 1s)`,
                                top: `calc(60vh + ${(t / 100) * 20}vh)`,
                                left: `calc(${l} * 1%)`,
                            }}/>
                    </div>   
                )
            }):''}
        </div>
    )
}

function TopSection({neon, rain}) {
    return (
        <div className={'pf-top-section'}>
            <Rain rain={rain}/>
            <Header neon={neon} />
        </div>
    )
}

export default TopSection;